import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  facebookColor: string = "hue-rotate(15deg)";
  gmailColor: string = "hue-rotate(150deg)";
  githubColor: string = "hue-rotate(60deg)";

  mouseEnter(social: string){
    document.getElementById("filtered").style.filter = "" + social + "";
  }

  mouseLeave(){
    document.getElementById("filtered").style.filter = "hue-rotate(0)";
  }
}



